.btn {
    border: none;
    padding: 10px 28px;
    margin: 5px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}

.btn:hover {
    background: lightsteelblue;
}


@media only screen and (max-width: 768px) {

    /* For mobile phones: */
    .btn {
        height: 2.5rem;
    }
}